.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}



div{
  width: 100%;
}

p{
  width: 100%;
}

button{
  margin-left: .2em;
}

button:hover{
  text-decoration: underline;
}

li, Link, a{
  margin-left: .2em;
  text-decoration: none;
  display: inline;
  color: black;
}

li:hover, Link:hover, a:hover{
  text-decoration: underline;
}
